import React, { useState } from 'react';

function BookNow() {
  // State to manage the booking form
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    date: '',
    time: '',
    serviceType: '',
  });

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    // Here, you can send the formData to a backend server for processing
    // You can use fetch() or Axios for making an HTTP request to your server
    console.log('Form data submitted:', formData);
    // Reset the form after submission
    setFormData({
      name: '',
      email: '',
      phone: '',
      date: '',
      time: '',
      serviceType: '',
    });
  };

  return (
    <div>
       <div><section class="content-container container mt-5">
  <div class="row">
         <div class="col-lg-3">
            
            <h3> left Side of Booking page</h3>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam nec mauris et purus vehicula ullamcorper.</p>
        </div>
      <div class="col-lg-6">
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="name">Name:</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="email">Email:</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="phone">Phone:</label>
          <input
            type="tel"
            id="phone"
            name="phone"
            value={formData.phone}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="date">Date:</label>
          <input
            type="date"
            id="date"
            name="date"
            value={formData.date}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="time">Time:</label>
          <input
            type="time"
            id="time"
            name="time"
            value={formData.time}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="serviceType">Service Type:</label>
          <select
            id="serviceType"
            name="serviceType"
            value={formData.serviceType}
            onChange={handleInputChange}
            required
          >
            <option value="">Select a service type</option>
            <option value="basic">Basic Cleaning</option>
            <option value="deep">Deep Cleaning</option>
            <option value="moveout">Move-out Cleaning</option>
          </select>
        </div>
        <button type="submit">Book Now</button>
      </form>
      </div>
      <div class="col-lg-3">
          <img src="https://via.placeholder.com/200" alt="Company" class="img-fluid"/>
      </div>
  </div>
</section></div>
        
    </div>
  );
}

export default BookNow;
