import React from 'react';
import GoogleMapReact from 'google-map-react';
const AnyReactComponent = ({ text }) => <div>{text}</div>;
const ContactMap = ({ apiKey }) => {
  return (
    <div style={{ height: '400px', width: '100%' }}>
       <GoogleMapReact
        bootstrapURLKeys={{ key: apiKey }}
        defaultCenter={{ lat: 37.7749, lng: -122.4194 }} // Center the map on San Francisco, CA
        defaultZoom={15} // Adjust the zoom level as needed
      >
        {/* You can add markers or other elements here */}
        <AnyReactComponent
          lat={37.7749}
          lng={-122.4194}
          text="Your Marker Text"
        />
      </GoogleMapReact>
    </div>
  );
};

export default ContactMap;
